import { useMemo } from "react"
import PropTypes from "prop-types"
import { Line } from "react-chartjs-2"
import Card from "@mui/material/Card"
import MDBox from "../../../../components/MDBox"
import MDTypography from "../../../../components/MDTypography"
import configs from "../../../../examples/Charts/LineCharts/ReportsLineChart/configs"
import DataLine from "../../../../examples/Charts/LineCharts/ReportsLineChartNoCard/configs/DataLine"

function ReportsLineChart({ color, title, description, date, chart, children, network_data }) {
  const { options } = configs(chart.labels || [], chart.datasets || {})

  const labels = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]

  const salesData = {
    labels,
    datasets: [
      {
        label: "Amount Invested",
        data: network_data?.amount,
        backgroundColor: "transparent",
        borderColor: "rgba(255, 255, 255, .8)",
        borderWidth: 4,
        fill: true,
        maxBarThickness: 6,
        pointBackgroundColor: "rgba(255, 255, 255, .8)",
        pointBorderColor: "transparent",
        pointRadius: 5,
        tension: 0
      }
    ]
  }

  return (
    <Card sx={{ height: "100%", px: 2 }}>
      <MDBox pt="1rem" pb="1.1rem">
        {useMemo(() => {
          return (
            <MDBox variant="gradient" bgColor={color} borderRadius="lg" coloredShadow={color} py={2} pr={0.5} mt={-5} height="12.5rem">
              {salesData && <Line data={salesData} options={options} />}
            </MDBox>
          )
        }, [options, color])}
        <MDBox pt={1} p={0.5}>
          <MDBox>
            <MDBox py={2} pb={1.5}>
              <MDBox pb={1.2} display="flex" justifyContent="space-between" alignItems="center">
                <MDTypography fontWeight={"medium"} sx={{ mr: 1 }} variant="button" textTransform="capitalize" fontSize={16}>
                  Total Networkers
                </MDTypography>
                <MDBox display="flex" justifyContent="space-between" flexDirection="column">
                  <MDTypography
                    mr="3px"
                    alignItems="baseline"
                    display="flex"
                    fontWeight="medium"
                    justifyContent="end"
                    color={color}
                    variant="button"
                    textTransform="capitalize"
                    fontSize={16}>
                    {network_data?.total_network.toLocaleString("en-US")}
                  </MDTypography>
                </MDBox>
              </MDBox>
              <MDBox display="flex" flexDirection="column" gap={0.2}>
                <DataLine
                  color="#3e0b9a"
                  label="1st Line"
                  amount={network_data?.first_line_network && network_data?.first_line_network.toLocaleString("en-US")}
                  isProperty
                  noSquare
                  valueColor="warning"
                />
                <DataLine
                  color="#191919"
                  label="2nd Line"
                  amount={network_data?.second_line_network && network_data?.second_line_network.toLocaleString("en-US")}
                  isProperty
                  noSquare
                  valueColor="warning"
                />
                <DataLine
                  color="#191919"
                  label="Other Lines"
                  amount={network_data?.other_line_network && network_data?.other_line_network.toLocaleString("en-US")}
                  isProperty
                  noSquare
                  valueColor="warning"
                />
              </MDBox>
            </MDBox>
            <MDBox py={1.5} pb={1}>
              <MDBox pb={1.2} display="flex" justifyContent="space-between" alignItems="center">
                <MDTypography fontWeight={"medium"} variant="button" textTransform="capitalize" fontSize={16}>
                  Total Network Investments
                </MDTypography>
                <MDBox display="flex" justifyContent="space-between" flexDirection="column" textAlign="right">
                  <MDTypography
                    textAlign="right"
                    alignItems="baseline"
                    display="flex"
                    fontWeight="medium"
                    justifyContent="end"
                    color={color}
                    variant="button"
                    textTransform="capitalize"
                    fontSize={16}>
                    {network_data?.total_investments.toLocaleString("en-US")} USDT
                  </MDTypography>
                </MDBox>
              </MDBox>
              <MDBox display="flex" flexDirection="column" gap={0.2}>
                <DataLine
                  color="#3e0b9a"
                  label="1st Line"
                  amount={`${network_data?.first_line_investments && network_data?.first_line_investments.toLocaleString("en-US")} USDT`}
                  isProperty
                  noSquare
                  valueColor="warning"
                />
                <DataLine
                  color="#191919"
                  label="2nd Line"
                  amount={`${network_data?.second_line_investments && network_data?.second_line_investments.toLocaleString("en-US")} USDT`}
                  isProperty
                  noSquare
                  valueColor="warning"
                />
                <DataLine
                  color="#191919"
                  label="Other Lines"
                  amount={`${network_data?.other_line_investments && network_data?.other_line_investments.toLocaleString("en-US")} USDT`}
                  isProperty
                  noSquare
                  valueColor="warning"
                />
              </MDBox>
            </MDBox>
          </MDBox>
        </MDBox>
      </MDBox>{" "}
      {children}
    </Card>
  )
}

ReportsLineChart.defaultProps = {
  color: "dark",
  description: ""
}

ReportsLineChart.propTypes = {
  color: PropTypes.oneOf(["primary", "secondary", "info", "success", "warning", "error", "dark"]),
  title: PropTypes.string.isRequired,
  description: PropTypes.oneOfType([PropTypes.string, PropTypes.node]),
  date: PropTypes.string.isRequired,
  chart: PropTypes.objectOf(PropTypes.oneOfType([PropTypes.array, PropTypes.object])).isRequired
}

export default ReportsLineChart
