import { useMemo, useState } from "react"
import PropTypes from "prop-types"
import { useTable, usePagination, useGlobalFilter, useAsyncDebounce, useSortBy } from "react-table"
import Table from "@mui/material/Table"
import TableBody from "@mui/material/TableBody"
import TableContainer from "@mui/material/TableContainer"
import TableRow from "@mui/material/TableRow"
import MDBox from "../../../components/MDBox"
import MDTypography from "../../../components/MDTypography"
import MDInput from "../../../components/MDInput"
import MDPagination from "../../../components/MDPagination"
import DataTableHeadCell from "../../../examples/Tables/DataTable/DataTableHeadCell"
import DataTableBodyCell from "../../../examples/Tables/DataTable/DataTableBodyCell"
import chevronRightIcon from "../../../icons/chevronRightIcon"
import chevronLeftIcon from "../../../icons/chevronLeftIcon"

function DataTable({ entriesPerPage, canSearch, showTotalEntries, table, pagination, isSorted, color = "secondary", className, noEndBorder }) {
  const columns = useMemo(() => table.columns, [table])
  const data = table.rows

  const tableInstance = useTable({ columns, data, initialState: { pageIndex: 0 } }, useGlobalFilter, useSortBy, usePagination)

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    rows,
    page,
    pageOptions,
    canPreviousPage,
    canNextPage,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    setGlobalFilter,
    state: { pageIndex, pageSize, globalFilter }
  } = tableInstance

  const setEntriesPerPage = value => setPageSize(value)

  const renderPagination = pageOptions.map(option => (
    <MDPagination item key={option} onClick={() => gotoPage(Number(option))} active={pageIndex === option}>
      {option + 1}
    </MDPagination>
  ))

  const handleInputPagination = ({ target: { value } }) => (value > pageOptions?.length || value < 0 ? gotoPage(0) : gotoPage(Number(value)))

  const customizedPageOptions = pageOptions.map(option => option + 1)

  const handleInputPaginationValue = ({ target: value }) => gotoPage(Number(value.value - 1))

  const [search, setSearch] = useState(globalFilter)

  const onSearchChange = useAsyncDebounce(value => {
    setGlobalFilter(value || undefined)
  }, 100)

  const setSortedValue = column => {
    let sortedValue

    if (isSorted && column.isSorted) {
      sortedValue = column.isSortedDesc ? "desc" : "asce"
    } else if (isSorted) {
      sortedValue = "none"
    } else {
      sortedValue = false
    }

    return sortedValue
  }

  const entriesStart = pageIndex === 0 ? pageIndex + 1 : pageIndex * pageSize + 1

  let entriesEnd

  if (pageIndex === 0) {
    entriesEnd = pageSize
  } else if (pageIndex === pageOptions?.length - 1) {
    entriesEnd = rows?.length
  } else {
    entriesEnd = pageSize * (pageIndex + 1)
  }

  return (
    <TableContainer sx={{ boxShadow: "none" }}>
      {canSearch ? (
        <MDBox display="flex" justifyContent="space-between" alignItems="center" p={3}>
          {canSearch && (
            <MDBox width="100%" ml="auto">
              <MDInput
                placeholder="Search..."
                value={search}
                size="small"
                fullWidth
                onChange={({ currentTarget }) => {
                  setSearch(search)
                  onSearchChange(currentTarget.value)
                }}
              />
            </MDBox>
          )}
        </MDBox>
      ) : null}

      <Table {...getTableProps()} data-test-id="my_networker">
        <MDBox component="thead" data-test-id="my_networker">
          {headerGroups &&
            headerGroups.map(headerGroup => (
              <TableRow {...headerGroup.getHeaderGroupProps()} data-test-id="my_networker">
                {headerGroup.headers.map((column, index) => (
                  <DataTableHeadCell
                    key={index}
                    data-test-id="my_networker"
                    color={color}
                    {...column.getHeaderProps(isSorted && column.getSortByToggleProps())}
                    width={column.width ? column.width : "auto"}
                    align={column.align ? column.align : "left"}
                    sorted={setSortedValue(column)}>
                    {column.render("Header")}
                  </DataTableHeadCell>
                ))}
              </TableRow>
            ))}
        </MDBox>

        <TableBody {...getTableBodyProps()} data-test-id="my_networker">
          {page.map((row, key) => {
            prepareRow(row)
            return (
              <TableRow {...row.getRowProps()} key={key} data-test-id="my_networker">
                {row.cells.map((cell, index) => (
                  <DataTableBodyCell
                    data-test-id="my_networker"
                    key={index}
                    noBorder={noEndBorder && rows?.length - 1 === key}
                    align={cell.column.align ? cell.column.align : "left"}
                    {...cell.getCellProps()}>
                    {cell.render("Cell")}
                  </DataTableBodyCell>
                ))}
              </TableRow>
            )
          })}
        </TableBody>
      </Table>

      <MDBox
        display="flex"
        flexDirection={{ xs: "column", sm: "row" }}
        justifyContent="space-between"
        alignItems={{ xs: "flex-start", sm: "center" }}
        p={!showTotalEntries && pageOptions?.length === 1 ? 0 : 3}
        className={className}>
        {showTotalEntries && (
          <MDBox mb={{ xs: 3, sm: 0 }}>
            <MDTypography variant="button" color="secondary" fontWeight="regular">
              Showing {entriesStart} to {entriesEnd} of {rows?.length} entries
            </MDTypography>
          </MDBox>
        )}
        {pageOptions?.length > 1 && (
          <MDPagination variant={pagination.variant ? pagination.variant : "gradient"} color={pagination.color ? pagination.color : "info"}>
            {canPreviousPage && (
              <MDPagination item onClick={() => previousPage()}>
                {chevronLeftIcon}
              </MDPagination>
            )}
            {renderPagination?.length > 6 ? (
              <MDBox width="5rem" mx={1}>
                <MDInput
                  inputProps={{ type: "number", min: 1, max: customizedPageOptions?.length }}
                  value={customizedPageOptions[pageIndex]}
                  onChange={(handleInputPagination, handleInputPaginationValue)}
                />
              </MDBox>
            ) : (
              renderPagination
            )}
            {canNextPage && (
              <MDPagination item onClick={() => nextPage()}>
                {chevronRightIcon}
              </MDPagination>
            )}
          </MDPagination>
        )}
      </MDBox>
    </TableContainer>
  )
}

DataTable.defaultProps = {
  entriesPerPage: { defaultValue: 30, entries: [5, 10, 15, 20, 25] },
  canSearch: false,
  showTotalEntries: true,
  pagination: { variant: "gradient", color: "info" },
  isSorted: true,
  noEndBorder: false
}

DataTable.propTypes = {
  entriesPerPage: PropTypes.oneOfType([
    PropTypes.shape({
      defaultValue: PropTypes.number,
      entries: PropTypes.arrayOf(PropTypes.number)
    }),
    PropTypes.bool
  ]),
  canSearch: PropTypes.bool,
  showTotalEntries: PropTypes.bool,
  table: PropTypes.objectOf(PropTypes.array).isRequired,
  pagination: PropTypes.shape({
    variant: PropTypes.oneOf(["contained", "gradient"]),
    color: PropTypes.oneOf(["primary", "secondary", "info", "success", "warning", "error", "dark", "light"])
  }),
  isSorted: PropTypes.bool,
  noEndBorder: PropTypes.bool
}

export default DataTable
