import PropTypes from "prop-types"
import MDBox from "../../../components/MDBox"
import { useMaterialUIController } from "../../../components/context"
import Icon from "@mdi/react"
import { mdiMenuUp } from "@mdi/js"
import { mdiMenuDown } from "@mdi/js"

function DataTableHeadCell({ width, children, sorted, align, color, ...rest }) {
  const [controller] = useMaterialUIController()
  const { darkMode } = controller

  return (
    <MDBox
      component="th"
      width={width}
      py={1.5}
      px={3}
      sx={({ palette: { light }, borders: { borderWidth } }) => ({
        borderBottom: `${borderWidth[1]} solid ${light.main}`
      })}>
      <MDBox
        {...rest}
        position="relative"
        textAlign={align}
        color={darkMode ? "white" : color}
        opacity={0.7}
        sx={({ typography: { size, fontWeightBold } }) => ({
          fontSize: size.xxs,
          fontWeight: fontWeightBold,
          textTransform: "uppercase",
          cursor: sorted && "pointer",
          userSelect: sorted && "none"
        })}>
        {children}
        {sorted && (
          <MDBox
            position="absolute"
            top={0}
            right={align !== "right" ? "1px" : 0}
            left={align === "right" ? "1px" : "unset"}
            sx={({ typography: { size } }) => ({
              fontSize: size.lg
            })}>
            <MDBox position="absolute" top={-6} color={sorted === "asce" ? "text" : color} opacity={sorted === "asce" ? 1 : 0.5}>
              <Icon path={mdiMenuUp} size={1} />
            </MDBox>
            <MDBox position="absolute" top={0} color={sorted === "desc" ? "text" : color} opacity={sorted === "desc" ? 1 : 0.5}>
              <Icon path={mdiMenuDown} size={1} />
            </MDBox>
          </MDBox>
        )}
      </MDBox>
    </MDBox>
  )
}

// Setting default values for the props of DataTableHeadCell
DataTableHeadCell.defaultProps = {
  width: "auto",
  sorted: "none",
  align: "left"
}

// Typechecking props for the DataTableHeadCell
DataTableHeadCell.propTypes = {
  width: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  children: PropTypes.node.isRequired,
  sorted: PropTypes.oneOf([false, "none", "asce", "desc"]),
  align: PropTypes.oneOf(["left", "right", "center"])
}

export default DataTableHeadCell
